import { FC } from 'react'

import {
  Card,
  CardHeader,
  Checkbox,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { Conference } from 'src/models/Conference'

import ConferenceNote from '../Conference/ConferenceNote'

interface AttendanceTableProps {
  className?: string
  conference: Conference
  handleAttendanceCheck(conferenceId: number, userId: number, isAttendance: boolean): Promise<void>
}

const AttendanceTable: FC<AttendanceTableProps> = ({ conference, handleAttendanceCheck }) => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Card>
      <CardHeader title={conference.title} />
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>이름</TableCell>
              {matches && <TableCell>학번</TableCell>}
              {matches && <TableCell>학과</TableCell>}
              <TableCell>출석 여부</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {conference.member.map(member => {
              return (
                <TableRow hover key={member.id}>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                      {member.name}
                      {!matches && (
                        <Typography variant="subtitle1" gutterBottom noWrap>
                          {member.studentId}
                        </Typography>
                      )}
                    </Typography>
                  </TableCell>

                  {matches && (
                    <TableCell>
                      <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                        {member.studentId}
                      </Typography>
                    </TableCell>
                  )}
                  {matches && (
                    <TableCell>
                      <Typography variant="body1" fontWeight="bold" color="text.primary" noWrap>
                        {member.department}
                      </Typography>
                    </TableCell>
                  )}

                  <TableCell>
                    <Checkbox
                      defaultChecked={member.attendanceType}
                      onChange={e =>
                        handleAttendanceCheck(conference.id, member.id, (e.target as HTMLInputElement).checked)
                      }
                    />
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <ConferenceNote conference={conference} />
    </Card>
  )
}

export default AttendanceTable
