import { useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Container, Grid } from '@mui/material'

import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import { type ClubMemberForAuth } from 'src/models/ClubMember'
import { Semester, getCurrentSemester, getCurrentYear } from 'src/utils/date'

import PageHeader from '../PageHeader'
import AttendanceList from './AttendanceList'

function AttendanceCheck() {
  const authUser = useAuthUser()

  const { id } = useParams()
  const clubId = Number(id)
  const club = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId)[0]
  const [year, setYear] = useState(getCurrentYear())
  const [semester, setSemester] = useState<Semester>(getCurrentSemester())

  const handleYearSet = (year: number) => {
    setYear(year)
  }

  const handleSemesterSet = (semester: Semester) => {
    setSemester(semester)
  }

  return (
    <>
      <Helmet>
        <title>출석 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} buttonText="행사 추가" link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <DateForm {...{ clubId, year, handleYearSet, semester, handleSemesterSet }} />
        <Grid container direction="row" justifyContent="center" alignItems="stretch" spacing={3}>
          <Grid item xs={12}>
            <AttendanceList />
          </Grid>
        </Grid>
      </Container>
    </>
  )
}

export default AttendanceCheck
