import axios from 'axios'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useRef } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useNavigate, useParams } from 'react-router-dom'

import { Box, Card, Typography } from '@mui/material'
import { Editor } from '@toast-ui/react-editor'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import TextEditor from 'src/components/TextEditor'
import { Conference } from 'src/models/Conference'

interface ConferenceNoteProps {
  conference: Conference
}

const ConferenceNote: FC<ConferenceNoteProps> = ({ conference }) => {
  const navigate = useNavigate()
  const { id } = useParams()
  const clubId = Number(id)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const editorRef = useRef<Editor>(null)

  const handleSubmit = () => {
    axios({
      method: 'put',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conference.id}/note`,
      data: {
        note: editorRef.current?.getInstance().getHTML()
      },
      headers: { Authorization: authHeader() }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
          navigate(`/manage/${clubId}/attendance/`)
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  useEffect(() => {
    editorRef.current?.getInstance().setHTML(conference.note || '')
  }, [])

  return (
    <Card>
      <Box padding={{ xs: 3, md: 6 }} paddingBottom={{ xs: 1, md: 3 }}>
        <Card>
          <TextEditor
            initialValue=""
            editorRef={editorRef}
            toolbarItems={[
              ['heading', 'bold', 'italic', 'strike'],
              ['hr', 'quote'],
              ['ul', 'ol', 'task'],
              ['table', 'link'],
              ['code', 'codeblock']
            ]}
          />
        </Card>
        <Box display="flex" justifyContent="right" sx={{ mr: 1, mt: 1 }}>
          <Typography variant="caption">{conference.log.length > 31 && conference.log}</Typography>
        </Box>
      </Box>
      <CancelSubmitBtns handleSubmit={handleSubmit} />
    </Card>
  )
}

export default ConferenceNote
