import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'

import {
  Box,
  Button,
  Card,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  type SelectChangeEvent
} from '@mui/material'

import ConfirmDialog from 'src/components/ConfirmDialog'
import { FIRST_SEMESTER, SECOND_SEMESTER } from 'src/data/semester'
import { STORAGE_KEY, Semester, getCurrentSemester, getCurrentYear, getSemesterName } from 'src/utils/date'

interface DateFormProps {
  clubId?: string | number
  year: number
  handleYearSet: (year: number) => void
  semester: Semester
  handleSemesterSet: (semester: Semester) => void
}

const DateForm: React.FC<DateFormProps> = ({ semester, year, handleSemesterSet, handleYearSet, clubId }) => {
  const [confirmMode, setConfirmMode] = useState<boolean>(false)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()

  const confirmMessage = `지금부터 생성되는 회의, 지원, 정보 수정 등의 모든 작업은 ${getCurrentYear()}년 ${getSemesterName(
    getCurrentSemester()
  )}를 기준으로 진행됩니다. 정말로 설정하시겠습니까?`

  const years = Array.from({ length: 11 }).map((_, index) => getCurrentYear() - index) // 10년 전부터 현재 년도까지의 년도 리스트

  useEffect(() => {
    removeLocalSemester()
    setLocalSemester(semester)
    if (year !== 0) {
      removeLocalYear()
      setLocalYear(year)
    }
  }, [semester, year])

  const setLocalSemester = (semester: string) => {
    window.localStorage.setItem(STORAGE_KEY.SEMESTER, semester)
  }
  const setLocalYear = (year: number) => {
    window.localStorage.setItem(STORAGE_KEY.YEAR, year.toString())
  }
  const removeLocalSemester = () => {
    window.localStorage.removeItem(STORAGE_KEY.SEMESTER)
  }
  const removeLocalYear = () => {
    window.localStorage.removeItem(STORAGE_KEY.YEAR)
  }

  const handleSemesterChange = (e: SelectChangeEvent<Semester>) => {
    e.preventDefault()
    handleSemesterSet(e.target.value === 'SPRING' ? 'SPRING' : 'FALL')
  }

  const handleYearChange = (e: SelectChangeEvent<number>) => {
    e.preventDefault()
    const selectedYear = Number(e.target.value)
    if (selectedYear === getCurrentYear() && getCurrentSemester() === FIRST_SEMESTER && semester === SECOND_SEMESTER) {
      enqueueSnackbar(`현재는 ${selectedYear}년 1학기입니다. 2학기는 선택할 수 없습니다.`)
      return
    }
    handleYearSet(Number(e.target.value))
  }

  const handleClubSemesterClick = () => {
    setConfirmMode(true)
  }

  const handleConfirmDialogClose = () => {
    setConfirmMode(false)
  }

  const handleSubmit = async () => {
    await axios({
      method: 'patch',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}`,
      data: {
        year: getCurrentYear(),
        semester: getCurrentSemester()
      },
      headers: { Authorization: authHeader() }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('학기 설정이 완료되었습니다.', { variant: 'success' })
          return
        }
        throw new Error('학기 설정에 실패했습니다.')
      })
      .catch(() => {
        enqueueSnackbar('학기 설정에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  return (
    <Box mb={1}>
      <Card sx={{ padding: { xs: '10px', md: '15px' } }}>
        <Grid container spacing={2}>
          <Grid item sm={2} xs={12} md={2}>
            <FormControl sx={{ width: '100%' }}>
              <InputLabel id="year-select-label">년도</InputLabel>
              <Select
                labelId="year-select-label"
                id="year-select"
                value={year}
                label="년도"
                sx={{ height: 40 }}
                onChange={handleYearChange}
              >
                {years.map(year => (
                  <MenuItem key={year} value={year}>
                    {year}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item sm={2} xs={12} md={2}>
            <FormControl sx={{ width: '100%' }}>
              <InputLabel id="semester-select-label">학기</InputLabel>
              <Select
                labelId="semester-select-label"
                id="semester-select"
                value={semester}
                label="학기"
                sx={{ height: 40 }}
                onChange={handleSemesterChange}
              >
                <MenuItem value={FIRST_SEMESTER}>1학기</MenuItem>
                <MenuItem value={SECOND_SEMESTER} disabled={year === getCurrentYear() && semester === FIRST_SEMESTER}>
                  2학기
                </MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid item sm={3} xs={12} md={2}>
            <Button
              onClick={handleClubSemesterClick}
              variant="outlined"
              sx={{ borderRadius: 1, height: 40, width: '100%' }}
            >
              학기 변경
            </Button>
          </Grid>
        </Grid>
      </Card>

      <ConfirmDialog
        open={confirmMode}
        handleClose={handleConfirmDialogClose}
        handleSubmit={handleSubmit}
        confirmMessage={confirmMessage}
      />
    </Box>
  )
}

export default DateForm
